<template>
  <div class="home" style="width: 100%;height: 100%">
    <div ref="myEcharts" style="width: 100%;height: 100%"></div>
  </div>
</template>

<script>
import person from '@/assets/person/personList.js'
export default {
  name: 'HomeView',
  data() {
    return {
      myChart: null
    }
  },
  mounted() {
    this.initEcharts()
  },
  methods: {
    initEcharts() {
      this.myChart = this.$echarts.init(this.$refs.myEcharts)
      window.onresize = this.myChart.resize
      this.myChart.setOption(this.getOptions(person.personList,person.lineList),true)
      this.myChart.resize()
    },
    /**
     * 获取echarts配置
     * @personList 人物列表
     * @lineList 人物关系连线
     * @return option
     */
    getOptions(personList,lineList) {
      let option = {
        title: {
          text: '名侦探柯南主要人物关系网'
        },
        // tooltip: {},
        // legend: {
        //   show: true,
        // },
        animationDurationUpdate: 500,
        animationEasingUpdate: 'quinticInOut',
        label: {
          normal: {
            show: true,
            textStyle: {
                fontSize: 12
            },
          }
        },
        series: [
          {
            type: 'graph',
            layout: 'force',
            symbolSize: 60,
            // focusNodeAdjacency: true, // 鼠标hover效果
            roam: true,
            // 节点分类
            categories: [
              {
                name: '黑方',
                itemStyle: {
                  color: "#222"
                }
              },
              {
                name: '红方',
                label: {
                  color: '#F71D06',
                  fontWeight: 'bold',
                }
              },
              {
                name: '中立',
                label: {
                  color: '#24acf2',
                  fontWeight: 'bold'
                }
              },
              {
                name: '组织',
                itemStyle: {
                  color: "#12cf6a"
                },
                label: {
                  color: '#222',
                  fontWeight: 'bold'
                }
              }
            ],
            // 节点文字
            label: {
              normal: {
                show: true,
                position: 'insideBottom',
                textStyle: {
                  fontSize: 12
                },
              }
            },
            // 力引导配置项
            force: {
              repulsion: 1000,
              edgeLength: 100
            },
            edgeSymbol: ['none', 'arrow'],
            edgeSymbolSize: [4, 10],
            // 连线
            edgeLabel: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: 10
                },
                position: 'middle',
                formatter: "{c}"
              }
            },
            data: personList,
            links: lineList,
            lineStyle: {
              normal: {
                opacity: 1,
                width: 1,
                curveness: 0
              }
            },
            markLine: {
              show: true,
              position: 'middle'
            },
            emphasis: {
              focus: 'adjacency'
            }
          }
        ]
      };
      return option
    }
  }
}
</script>
